import React from "react";
import Form from "../components/Form";
import Report from "../components/Report";

class RenderForm extends React.Component {
  state = {
    form: {
      num_hisopados: "",
      num_nuevos_positivos: "",
      num_positivos: "",
      num_activos: "",
      num_alta: "",
    },
  };

  handleChange = (e) => {
    this.setState({
      form: {
        ...this.state.form,
        [e.target.name]: e.target.value,
      },
    });
  };

  render() {
    return (
      <div>
        <Report {...this.state.form} />
        <Form onChange={this.handleChange} form={this.state.form} />
      </div>
    );
  }
}

export default RenderForm;
