import React from "react";
import logo from "../images/logo.png";
import "./Report.css";

function getDate() {
  let date = new Date();
  let meses = new Array(
    "Enero",
    "Febrero",
    "Marzo",
    "Abril",
    "Mayo",
    "Junio",
    "Julio",
    "Agosto",
    "Septiembre",
    "Octubre",
    "Noviembre",
    "Diciembre"
  );

  let FullDate =
    date.getDate() +
    " de " +
    meses[date.getMonth()] +
    " de " +
    date.getFullYear();

  return FullDate;
}

class Report extends React.Component {
  render() {
    const {
      num_hisopados,
      num_nuevos_positivos,
      num_positivos,
      num_activos,
      num_alta,
    } = this.props;
    return (
      <div className="Report-Container" id="ereport">
        <div className="Report-Header-Img">
          <img
            src={logo}
            alt="Municipalidad de Santa Catalina Holmberg"
            className="Report-Header-Img"
          />
        </div>
        <div className="Report-Header-Title">
          <h1>
            <span>Municipalidad de</span> <br />
            Santa Catalina Holmberg
          </h1>
          <h2>Dispensario Municipal</h2>
        </div>
        <div className="Report-Data">
          <h3>Reporte Epidemiológico</h3>
          <hr />
          <strong>{getDate()}</strong>
          <table>
            <thead>
              <tr>
                <td>Hisopados del día:</td>
                <td>{num_hisopados}</td>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>Nuevos casos positivos:</td>
                <td>{num_nuevos_positivos}</td>
              </tr>
              <tr>
                <td>Total de casos positivos:</td>
                <td>{num_positivos}</td>
              </tr>
              <tr>
                <td>Casos activos:</td>
                <td>{num_activos}</td>
              </tr>
              <tr>
                <td>Total de casos dados de alta:</td>
                <td>{num_alta}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    );
  }
}

export default Report;
